
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import { setTimeout } from 'timers';

export default {
name:"teamM1",
data(){
return {
chartSH:false,
chartSHTime:false,
waterStartTitle:"请选择开始时间",
waterEndTitle:"请选择结束时间",
waterStartTime:"",
waterEndTime:"",
wHospVal:"",
wServerVal:"",
wUserVal:"",
addTile:"请选择地区",
addList:[],//所有地区
addVal:[],//选择地区
addId:"",
aresId:"",
}
},
components:{
Group,
Datetime,
XInput,
PopupPicker,
Alert
},
created(){
this.addressFn()
},
methods:{
addressFn(){

var addData = require('../../../static/address/region.json');

let newArr1=[];
addData.forEach((item1) => {
newArr1.push({
value: ""+item1.value,
name: item1.label,
prent: 0
})
let parentC = ""+item1.value
item1.children.forEach((item2) => {
newArr1.push({
value: ""+item2.value,
name: item2.label,
parent : ""+parentC
})
})
})

this.addList = newArr1
},

timeChange1(value){
this.waterStartTime = value;
if(value){
this.waterStartTitle="";
}
},
timeChange2(value){
this.waterEndTime = value;
if(value){
this.waterEndTitle="";
}
},

addBtn(){
let region_id1,region_id2;
region_id1 = this.addVal[0]
region_id2 = this.addVal[1]
console.log(region_id1,region_id2)
if(region_id1!=""){
this.addTile=""
}
this.addId = region_id1;
this.aresId = region_id2;
},
reflashPage(){
let NewPage = '_empty' + '?time=' + new Date().getTime()/10;
this.$router.push(NewPage);
this.$router.go(-1);
this.LoadingSH = false
},

resetBtn(){
this.reflashPage()
this.waterStartTime="";
this.waterStartTitle="请选择开始时间";
this.waterEndTime="";
this.waterEndTitle="请选择结束时间";
this.wHospVal="";
this.wServerVal="";
this.wUserVal="";
localStorage.removeItem("waterStartTime");
localStorage.removeItem("waterEndTime");
localStorage.removeItem("wHospVal");
localStorage.removeItem("wServerVal")
localStorage.removeItem("wUserVal")
localStorage.removeItem("runAddId1")
localStorage.removeItem("runAddId2")
},

queryBtn(){





if(this.addId==""&&this.waterStartTime==""&&this.waterEndTime==""&&this.wHospVal==""&&this.wServerVal==[]&&this.wUserVal==""){
this.chartSH = true;
}else{
let newS = this.waterStartTime.replace("//-/g", "//")
let newE = this.waterEndTime.replace("//-/g", "//")
if(newS>newE){
this.chartSHTime = true
}else{
localStorage.setItem("waterStartTime",this.waterStartTime);
localStorage.setItem("waterEndTime",this.waterEndTime);
localStorage.setItem("wHospVal",this.wHospVal);
localStorage.setItem("wServerVal",this.wServerVal)
localStorage.setItem("wUserVal",this.wUserVal)
localStorage.setItem("runAddId1",this.addId)
localStorage.setItem("runAddId2",this.aresId)
this.$router.push({path:'runwater'});
}
}
}
}
}



