
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Alert from 'vux/src/components/alert/index.vue'


import axios from 'axios'

export default {
name:"teamM1",
data(){
return {
chartSH:false,
chartSHTime:false,
makeStartTitle:"请选择预约开始时间",
makeEndTitle:"请选择预约结束时间",
makeStartTime:"",
makeEndTime:"",
hospVal:"",
serverVal:"",
userVal:"",
cateTile:"请选择分类",
cateList:[],//所有分类
cateVal:[],//选择分类
cateId:"",
addTile:"请选择地区",
addList:[],//所有地区
addVal:[],//选择地区
addId:"",
aresId:"",
}
},
components:{
Group,
Datetime,
XInput,
PopupPicker,
Alert
},
created(){
this.addressFn()
axios.get('/v1/getCateList')
.then((response)=>{


let oldList = response.data;

let newArr=[];
oldList.forEach((item1) => {
newArr.push({
value: ""+item1.phone,
name: item1.label,
prent: 0
})
let parentC = ""+item1.phone
item1.children.forEach((item2) => {
newArr.push({
value: ""+item2.value,
name: item2.label,
parent : ""+parentC
})
let parentCc = item2.value
item2.children.forEach((item3) => {
newArr.push({
value: ""+item3.value,
name: item3.label,
parent : ""+parentCc
})
})
})
})
this.cateList = newArr
})
.catch((error)=>{
console.log(error)
})
},
methods:{
addressFn(){

var addData = require('../../../static/address/region.json');

let newArr1=[];
addData.forEach((item1) => {
newArr1.push({
value: ""+item1.value,
name: item1.label,
prent: 0
})
let parentC = ""+item1.value
item1.children.forEach((item2) => {
newArr1.push({
value: ""+item2.value,
name: item2.label,
parent : ""+parentC
})
})
})

this.addList = newArr1
},

timeChange1(value){
this.makeStartTime = value;
if(value){
this.makeStartTitle="";
}
},
timeChange2(value){
this.makeEndTime = value;
if(value){
this.makeEndTitle="";
}
},

cateBtn(){
let cate_id;

for(var i=0 ;i<this.cateVal.length;i++){
if(this.cateVal[this.cateVal.length-1]){
cate_id = this.cateVal[this.cateVal.length-1]
}else{
cate_id = this.cateVal[this.cateVal.length-2]
}
}
if(cate_id!=""){
this.cateTile=""
}
this.cateId = cate_id

},

addBtn(){
let region_id1,region_id2;
region_id1 = this.addVal[0]
region_id2 = this.addVal[1]
console.log(region_id1,region_id2)
if(region_id1!=""){
this.addTile=""
}
this.addId = region_id1;
this.aresId = region_id2;
},
reflashPage(){
let NewPage = '_empty' + '?time=' + new Date().getTime()/10;
this.$router.push(NewPage);
this.$router.go(-1);
this.LoadingSH = false
},

resetBtn(){
this.reflashPage()
this.makeStartTime="";
this.makeStartTitle="请选择预约开始时间";
this.makeEndTime="";
this.makeEndTitle="请选择预约结束时间";
this.hospVal="";
this.serverVal="";
this.userVal="";
localStorage.removeItem("makeStartTime");
localStorage.removeItem("makeEndTime");
localStorage.removeItem("hospVal");
localStorage.removeItem("serverVal")
localStorage.removeItem("userVal")
localStorage.removeItem("makeCateId")
localStorage.removeItem("makeAddId1")
localStorage.removeItem("makeAddId2")
},

queryBtn(){
if(this.addId==""&&this.cateId==""&&this.makeStartTime==""&&this.makeEndTime==""&&this.hospVal==""&&this.serverVal==[]&&this.userVal==""){
this.chartSH = true;
}else{
let newS = this.makeStartTime.replace("//-/g", "//")
let newE = this.makeEndTime.replace("//-/g", "//")
if(newS>newE){
this.chartSHTime = true
}else{
localStorage.setItem("makeStartTime",this.makeStartTime);
localStorage.setItem("makeEndTime",this.makeEndTime);
localStorage.setItem("hospVal",this.hospVal);
localStorage.setItem("serverVal",this.serverVal)
localStorage.setItem("userVal",this.userVal)
localStorage.setItem("makeCateId",this.cateId)
localStorage.setItem("makeAddId1",this.addId)
localStorage.setItem("makeAddId2",this.aresId)
this.$router.push({path:'make'});
}
}
}
}
}



