
import axios from 'axios'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VArea from 'vux/src/components/v-chart/v-area.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VAxis from 'vux/src/components/v-chart/v-axis.vue'
import VPoint from 'vux/src/components/v-chart/v-point.vue'


export default {
name: 'indexM4',
data(){
return {
timeImg:require("../../../static/img/timeImg.png"),
show:false,
startTime:"",
endTime:"",
alertSH:false,
chartSH:false,
chartSHTime:false,
chartMsg:"",
modelTime:"",
postTime:[],
gradient: [
[0, '#A63EF0'],
[1, '#A63EF0']
],
data: [
{ time: "2019-03-28", tem:0 },
]
}
},
components:{
Popup,
Group,
Calendar,
Datetime,
XButton,
Alert,
VChart,
VLine,
VArea,
VTooltip,
VScale,
VAxis ,
VPoint
},
created(){

this.userFn()


},
methods:{
userFn(){
axios.post('/v1/userinfo')
.then((response)=>{

localStorage.setItem("userId",response.data.data.users.user_id);

let dateTime = {

"service_id":response.data.data.users.user_id,
"time" : this.postTime
}
let that = this
axios.post('/v1/server_achievement',this.$qs.stringify(dateTime))
.then((response)=>{
let startTimes,endTimes;
for( let i=0;i<response.data.data.length;i++){
startTimes = response.data.data[0].time;
endTimes = response.data.data[response.data.data.length-1].time;
}
let xS = startTimes.replace(/-/g, "/")
let xE = endTimes.replace(/-/g, "/")
that.modelTime = xS+"—"+xE;
let meter = response.data.data
let obj = meter.map(function (item) {
return{
"tem":item.achievement,
"time":item.time
}
})
that.data = obj
let thats = that
if(obj.length==0){
thats.chartSH = true
thats.chartMsg = "开始时间和结束时间选错或选择的时间段没有数据"
}

})
.catch((error)=>{
this.chartSH = true
this.chartMsg = error.response.data.message
console.log(error.response.data.message)
})
})
.catch((error)=>{

})
},

popupClick(){
this.show = true
},

determine(){
if(this.startTime==""||this.endTime==""){
this.alertSH = true
}else{
let time = [this.startTime,this.endTime];
this.postTime =[this.startTime,this.endTime];
let newS = this.startTime.replace("//-/g", "//")
let newE = this.endTime.replace("//-/g", "//")
let xS = this.startTime.replace(/-/g, "/")
let xE = this.endTime.replace(/-/g, "/")
if(newS>newE){
this.chartSHTime = true
}else{
this.modelTime = xS+"—"+xE;
this.show = false;
this.userFn()
}

}
},

popupClose(){
this.show = false
},

chartData(){
































},
beforeDestroy() {
this.data = [{ time: "2019-03-28", tem:0 }]
}
}
}



