
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Alert from 'vux/src/components/alert/index.vue'


import axios from 'axios'
import { setTimeout } from 'timers';

export default {
name:"teamM1",
data(){
return {
chartSH:false,
chartSHTime:false,
subStartTitle:"请选择开始时间",
subEndTitle:"请选择结束时间",
subStartTime:"",
subEndTime:"",
sHospVal:"",
sServerVal:"",
sUserVal:"",
cateTile:"请选择分类",
cateList:[],//所有分类
cateVal:[],//选择分类
cateId:"",
addTile:"请选择地区",
addList:[],//所有地区
addVal:[],//选择地区
addId:"",
aresId:"",
LoadingSH:false,
}
},
components:{
Group,
Datetime,
XInput,
PopupPicker,
Alert
},
created(){
this.cateFn();
this.addressFn();
},
methods:{
cateFn(){

axios.get('/v1/getCateList')
.then((response)=>{


let oldList = response.data;

let newArr=[];
oldList.forEach((item1) => {
newArr.push({
value: ""+item1.phone,
name: item1.label,
prent: 0
})
let parentC = ""+item1.phone
item1.children.forEach((item2) => {
newArr.push({
value: ""+item2.value,
name: item2.label,
parent : ""+parentC
})
let parentCc = item2.value
item2.children.forEach((item3) => {
newArr.push({
value: ""+item3.value,
name: item3.label,
parent : ""+parentCc
})
})
})
})
this.cateList = newArr
})
.catch((error)=>{
console.log(error)
})
},
addressFn(){

var addData = require('../../../static/address/region.json');

let newArr1=[];
addData.forEach((item1) => {
newArr1.push({
value: ""+item1.value,
name: item1.label,
prent: 0
})
let parentC = ""+item1.value
item1.children.forEach((item2) => {
newArr1.push({
value: ""+item2.value,
name: item2.label,
parent : ""+parentC
})
})
})

this.addList = newArr1
},

timeChange1(value){
this.subStartTime = value;
if(value){
this.subStartTitle="";
}
},
timeChange2(value){
this.subEndTime = value;
if(value){
this.subEndTitle="";
}
},

cateBtn(){
let cate_id;

for(var i=0 ;i<this.cateVal.length;i++){
if(this.cateVal[this.cateVal.length-1]){
cate_id = this.cateVal[this.cateVal.length-1]
}else{
cate_id = this.cateVal[this.cateVal.length-2]
}
}
if(cate_id!=""){
this.cateTile=""
}
this.cateId = cate_id
console.log(this.cateId)
},

addBtn(){
let region_id1,region_id2;
region_id1 = this.addVal[0]
region_id2 = this.addVal[1]
console.log(region_id1,region_id2)
if(region_id1!=""){
this.addTile=""
}
this.addId = region_id1;
this.aresId = region_id2;
},
reflashPage(){
let NewPage = '_empty' + '?time=' + new Date().getTime()/10;
this.$router.push(NewPage);
this.$router.go(-1);
this.LoadingSH = false
},

resetBtn(){
this.reflashPage()
this.subStartTime="";
this.subStartTitle="请选择开始时间";
this.subEndTime="";
this.subEndTitle="请选择结束时间";
this.sHospVal="";
this.sServerVal="";
this.sUserVal="";







localStorage.removeItem("subStartTime");
localStorage.removeItem("subEndTime");
localStorage.removeItem("sHospVal");
localStorage.removeItem("sServerVal")
localStorage.removeItem("sUserVal")
localStorage.removeItem("subCateId")
localStorage.removeItem("subAddId1")
localStorage.removeItem("subAddId2")
},

queryBtn(){





if(this.addId==""&&this.cateId==""&&this.subStartTime==""&&this.subEndTime==""&&this.sHospVal==""&&this.sServerVal==[]&&this.sUserVal==""){
this.chartSH = true;
}else{
let newS = this.subStartTime.replace("//-/g", "//")
let newE = this.subEndTime.replace("//-/g", "//")
if(newS>newE){
this.chartSHTime = true
}else{
localStorage.setItem("subStartTime",this.subStartTime);
localStorage.setItem("subEndTime",this.subEndTime);
localStorage.setItem("sHospVal",this.sHospVal);
localStorage.setItem("sServerVal",this.sServerVal)
localStorage.setItem("sUserVal",this.sUserVal)
localStorage.setItem("subCateId",this.cateId)
localStorage.setItem("subAddId1",this.addId)
localStorage.setItem("subAddId2",this.aresId)
this.$router.push({path:'subsidies'});
}
}
}
}
}



